import React, {useState} from "react";
import { isMobileView } from "../utils/MobileView";
import { ToastContainer, toast } from "react-toastify";
import Loader from 'react-loader-spinner'
import "react-toastify/dist/ReactToastify.css";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import GuestService from '../auth/GuestService';


const SubmitProgramPoint = () => {

  const [performance, setPerformance] = useState({ name: "", email: "", type: "", time: "", equipment:"" });
  const [showSpinner, setShowSpinner] = useState(false);

  const onChange = (e) => {
    setPerformance({ ...performance, [e.target.name]: e.target.value });
  };

  const onSubmit = (e) => {
    e.preventDefault();
    setShowSpinner(true);
    GuestService.registerPerformance(performance).then((data) => {
      setShowSpinner(false);
      notify(data);
    });
  };

  const notify = (data) => {
    toast(`Uppträdande med ${data.name} har anmälts!`);
  };

  return (
    <div >
      <div className={isMobileView() ? "mobileProgramDiv" : "desktopProgramDiv"}>
        <h3 className={isMobileView() ? "mobileDinnerHeader" : "desktopFormHeader"}>
          Anmäl programpunkt
        </h3>
        <form onSubmit={onSubmit}>
          <p>Namn</p>
          <input
            type="textField"
            name="name"
            placeholder="Namn"
            className="formItemDesktop"
            value={performance.name}
            onChange={onChange}
            required
          ></input>
          <p>Epost</p>
          <input
            type="textField"
            name="email"
            placeholder="Epost"
            className="formItemDesktop"
            value={performance.email}
            onChange={onChange}
            required
          ></input>
          <p>Typ av uppträdande</p>
          <input
            type="textField"
            name="type"
            placeholder="Typ av uppträdande"
            className="formItemDesktop"
            value={performance.type}
            onChange={onChange}
            required
          ></input>
          <p>Beräknad tid</p>
          <input
            type="textField"
            name="time"
            placeholder="Beräknad tid"
            className="formItemDesktop"
            value={performance.time}
            onChange={onChange}
            required
          ></input>
          <p>Utrustning</p>
          <input
            type="textField"
            name="equipment"
            placeholder="Utrustning"
            className="formItemDesktop"
            value={performance.equipment}
            onChange={onChange}
          ></input>
          <p>Introlåt</p>
          <input
            type="textField"
            name="song"
            placeholder="Introlåt"
            className="formItemDesktop"
            value={performance.song}
            onChange={onChange}
          ></input>
          <button type="submit">Skicka in</button>
          <ToastContainer/>
          { showSpinner &&  
          (<Loader type="Puff"
            color="#757575"
            height={100}
            width={100}
            timeout={3000}/>)}
        </form>
      </div>
    </div>
  );
};

export default SubmitProgramPoint;

